import styled from 'styled-components';
import { black } from 'theme/variables';
import { Relative, Flex } from 'theme/grid';
import PlayButton from 'components/PlayButton/PlayButton';
import media from 'theme/media';

export const Title = styled.h1`
    margin-top: 0;
    color: ${black};
    font-weight: bold;
`;

export const VideoContainer = styled(Relative) `
    width: 60%;    
    margin-left: 50px;
    margin-right: 50px;  
    ${media.tablet`
        width: 100%;
        margin-left: 0;
    `}
    flex-shrink: 0;
    flex-grow: 0;
    cursor: pointer;
`;

export const StyledPlayButton = styled(PlayButton) `
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: opacity .3s;
    ${({ playing }) => playing && `
        opacity: 0;
    `}
`;

export const AppleShot1 = styled(Flex)`
    ${media.tablet`
        flex-direction: column;
    `}
`;



